import { POSSystem } from "@/components/pos/Cart";
import { getServerSession } from "next-auth";
import { authOptions } from "@/lib/auth";
import { redirect } from "next/navigation";
import { LogoutButton } from "@/components/shared/LogoutButton";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";

export default async function POSPage() {
    const session = await getServerSession(authOptions);

    if (!session?.user) {
        redirect("/login");
    }

    return (
        <div className="min-h-screen bg-zinc-50 dark:bg-black">
            <header className="px-6 py-3 border-b border-border flex justify-between items-center bg-white/80 dark:bg-zinc-950/80 backdrop-blur-md sticky top-0 z-50">
                <div className="flex items-center gap-4">
                    <h1 className="text-2xl font-black text-primary italic tracking-tighter">APRIL.POS</h1>
                    <Badge variant="outline" className="font-black text-[10px] tracking-widest border-primary/20 bg-primary/5 text-primary">
                        LIVE SYSTEM
                    </Badge>
                </div>

                <div className="flex gap-6 items-center">
                    <div className="flex items-center gap-2 px-3 py-1 bg-emerald-500/10 text-emerald-500 rounded-full border border-emerald-500/20">
                        <div className="w-2 h-2 bg-emerald-500 animate-pulse rounded-full shadow-[0_0_8px_rgba(16,185,129,0.5)]" />
                        <span className="text-[10px] font-bold uppercase tracking-wider">
                            {session.user.role} SESSION
                        </span>
                    </div>

                    <div className="flex items-center gap-3 border-l border-border pl-6">
                        <div className="text-right hidden sm:block">
                            <p className="text-sm font-black text-foreground leading-none tracking-tight">{session.user.name}</p>
                            <p className="text-[10px] text-muted-foreground mt-1 uppercase font-bold tracking-widest">Active Cashier</p>
                        </div>
                        <Avatar className="h-10 w-10 border-2 border-primary/20 shadow-lg shadow-primary/10">
                            <AvatarFallback className="bg-gradient-to-br from-primary to-purple-600 text-white font-black">
                                {session.user.name?.charAt(0) || "U"}
                            </AvatarFallback>
                        </Avatar>
                        <LogoutButton />
                    </div>
                </div>
            </header>

            <main className="container mx-auto p-6">
                <POSSystem cashierId={session.user.id} />
            </main>
        </div>
    );
}
